body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-tap-highlight-color: transparent;
}
nav {
  height: 50px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  background: rgba(255,255,255,1);
}
nav img {
	height: 25px;
    left: 5%;
    top: 10px;
    position: absolute;
}
nav ul {
	position: absolute;
	top: 6px;
	right: 5%;
	font-size: 0.8rem;
}
nav i {
	font-size: 1.1rem;
	cursor: pointer;
}
nav li {
	display: inline-block;
	margin-left: 60px;
}
.nav li a, .nav li i {
	text-decoration: none;
	color: #7B8689;
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
.nav li :hover {
	color: black;
}
.nav-mobile {
	display: none;
}
.nav-mobile li {
	margin-left: 30px;
	color: black;
}
.nav-mobile i {
	font-size: 1.2rem;
}
.toggle-nav {
	position: fixed;
	top: 50px;
	background: white;
	width: 100%;
	text-align: center;
	z-index: 100;
	height: 0;
	overflow: hidden;
	-webkit-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
	display: none;
}
.toggle-nav ul {
	list-style: none;
	padding-left: 0;
}
.toggle-nav li {
	margin: 30px auto;
}
.toggle-nav a {
	text-decoration: none;
	color: black;
}
#title{
	background: #f6f6f6;
	text-align: center;
	padding: 2rem;
	font-family: 'Montserrat', sans-serif;
	margin-top: 40px;
}
#title p{
	font-weight: 400;
}
@media only screen and (max-width: 768px) {
	.nav{
		display: none;
	}
	.nav-mobile{
		display: block;
	}
	.toggle-nav{
		display: block;
	}
}
.menu-container {
	height: 31px;
}
#menu {
	background: #f0f0f0;
	width: 100%;
	height: 31px;
	margin: 0 auto;
	text-align: center;
	z-index: 99;
	padding: 5px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
#menu span{
	padding: 20px;
}
#menu a {
	text-decoration: none;
	color:rgba(0,0,0,0.6);
	font-size: 1.1rem;
}
#menu a:hover {
	color: #92be48;
	opacity: 0.6;
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
#breakfast,#lunch,#dinner{
	position: relative;
	top: -60px;
	visibility: hidden;
}
.products-container h2{
	text-align: center;
	padding: 1rem;
	font-size: 1.5rem;
}
.grey-background{
	background: #f6f6f6;
}
.products-grid{
	padding-bottom: 30px;
	display: grid;
	grid-template-columns: repeat(auto-fit,minmax(320px,1fr));
	grid-auto-flow: row;
	justify-items: center;
	grid-row-gap: 40px;
	text-align: center;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	font-family: 'Montserrat', sans-serif;
	font-size: 0.9rem;
}
.product-container img {
	display: block;
	margin: 0 auto;
	width: 300px;
	cursor: pointer;
}
/* .product-container img:hover {
	box-shadow: inset rgba(143,160,241,.2) 0 0 0 1px, rgba(213,220,247,.59) 0 10px 20px;
} */
.product-container p {
	width: 300px;
	height: 30px;
	cursor: pointer;
}
.product-container select {
	width: 210px;
	height: 30px;
	background: white;
	border: 1px solid black;
	outline: none;
	cursor: pointer;
	font-family: 'Montserrat', sans-serif;
	font-size: 0.8rem;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	padding-left: 10px;
	margin-right: 3px;
}
.select {
	display: inline-block;
	position: relative;
}
.select::after {
	content: "\f078";
	font-family: "Font Awesome 5 Free";
	font-weight: 900;
	position: absolute;
	right: 12px;
	top: 22%;
	font-size: 1.1rem;
	pointer-events: none;
}
.product-container i {
	margin-left: 8px;
	font-size: 0.9rem;
	padding: 5px;
	cursor: pointer;
	border: 1px solid black;
	border-radius: 4px;
	width: 16px;
	height: 16px;
	color: white;
	background: black;
}
.nutrition span{
	padding: 0.3rem;
	display: inline-block;
	width: 5rem;
	font-size: 0.8rem;
}
.nutrition {
	background: rgba(0,0,0,0.6);
	color: white;
	-webkit-transition: all 0.5s ease;
	-o-transition: all 0.5s ease;
	transition: all 0.5s ease;
	pointer-events: none;
	width: 300px;
	position: absolute;
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
}


